import React from "react";

const Resume = ({ data }) => {
  if (data) {
    var skillset = data.skillset.map(function (skill) {
      return (
        <div key={skill.title}>
          <h3>{skill.title}</h3>
          <p className="info">{skill.skills}</p>
          <p>{skill.description}</p>
        </div>
      );
    });
    // var work = data.work.map(function (work) {
    //   return (
    //     <div key={work.company}>
    //       <h3>{work.company}</h3>
    //       <p className="info">
    //         {work.title}
    //         <span>&bull;</span> <em className="date">{work.years}</em>
    //       </p>
    //       <p>{work.description}</p>
    //     </div>
    //   );
    // });
    var stacks = data.stacks.map(function (stack) {
      var className = "bar-expand " + stack.name.toLowerCase();
      return (
        <li key={stack.name}>
          <div className="bar-container">
            <span style={{ width: stack.level }} className={className}></span>
          </div>
          <em>{stack.name}</em>
        </li>
      );
    });
  }

  return (
    <section id="resume">
      <div className="row education">
        <div className="three columns header-col">
          <h1>
            <span>Skills</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <div className="row item">
            <div className="twelve columns">{skillset}</div>
          </div>
        </div>
      </div>

      {/* <div className="row work">
        <div className="three columns header-col">
          <h1>
            <span>Work</span>
          </h1>
        </div>

        <div className="nine columns main-col">{work}</div>
      </div> */}

      <div className="row skill">
        <div className="three columns header-col">
          <h1>
            <span>Stacks</span>
          </h1>
        </div>

        <div className="nine columns main-col">
          <div className="bars">
            <ul className="skills">{stacks}</ul>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Resume;
